.title_input {
  border: 1px solid #d2e4f4;
  padding: 20px;
  padding-bottom: 10px;
  border-radius: 5px;
  margin-bottom: 20px;
}
/* 时间轴内容 */
.time_line_all .time_line_content {
  margin-left: 20px;
  border: 1px solid #d2e4f4;
  border-radius: 3px;
}
.time_line_all .time_line_content .time_line_title {
  height: 40px;
  background: #e9f4fe;
  line-height: 40px;
  padding-left: 10px;
}
.time_line_all .time_line_content .time_line_title span {
  font-weight: 600;
}
.time_line_all .time_line_content .time_line_title > span:nth-child(2) {
  margin-left: 20px;
}
.time_line_all .time_line_ul li {
  margin-bottom: 10px;
}
.time_line_all .time_line_ul li > span:nth-child(1) {
  display: inline-block;
  width: 100px;
}
.time_line_all .time_line_ul li > span {
  font-size: 12px;
  color: #a1b1be;
  vertical-align: top;
}
.time_line_all .time_line_ul li .time_line_img {
  width: 300px;
  display: inline-block;
  vertical-align: top;
}
.time_line_all .time_line_ul li .time_line_audio {
  vertical-align: top;
}
.time_line_all .time_line_ul .time_line_li_header {
  font-size: 14px;
  font-weight: 600;
  padding: 20px;
  padding-left: 0;
}
